import React, {Component} from "react";
import {connect} from "react-redux";
import { getIsSmsVerifiedCustomer,getIsOnlineCookie} from "eshop/modules/auth/selectors/authorization";

class MulticartStaticTextComponent extends Component {

    toHTML(descriptions) {
        let value = "";
        if(this.props.isOnlineCookie) {
            value = this.props.descriptions.textCaap
        } else if(this.props.smsVerified) {
            value = this.props.descriptions.textSms
        } else {
            value = this.props.descriptions.text
        }
        return {__html: value};
    }

    render() {
        return (<div dangerouslySetInnerHTML={this.toHTML(this.props.descriptions)}/>)
    }
}


const mapStateToProps = state => ({
    smsVerified : getIsSmsVerifiedCustomer(state),
    isOnlineCookie: getIsOnlineCookie(state)
})

const mapDispatchToProps = dispatch => ({
})


export default connect(mapStateToProps, mapDispatchToProps)(MulticartStaticTextComponent);